
// top-line
.top-line {
	background: #21191D;
	&__wrap {
		display: flex;
		justify-content: space-between;
		padding: 0.5rem 0;
		align-content: center;
		align-items: center; }
	&__burger {
		display: flex;
		align-items: center;
		@include rmin($md) {
			display: none; }
		&-link {
			display: inline-block;
			& span {
				height: 1px;
				width: 1.875rem;
				display: block;
				margin: 0.425rem 0;
				background: $firstColor; } } }
	&__logo {
		display: inline-block;
		padding: 0.25rem 0;
		@include rmin($md) {
			display: none; }
		& img {
			width: 8.5rem; } }
	&__mphone {
		display: inline-block;
		@include rmin($md) {
			display: none; }
		& img {
			width: 1.5rem; } }
	&__contact {
		color: #ffffff;
		align-content: center;
		align-items: center;
		display: none;
		@include rmin($md) {
			display: flex; } }
	&__address {
		color: rgba(255, 255, 255, 0.87); }
	&__phone {
		font-size: 1.125rem;
		margin-left: 3rem;
		padding-left: 2rem;
		font-weight: 900;
		position: relative;
		&:after {
			position: absolute;
			left: 0;
			top: 0.125rem;
			content: ' ';
			width: 1.25rem;
			height: 1.25rem;
			background-image: url(../img/icon/phone.svg);
			background-repeat: no-repeat;
			background-size: contain; } }
	&__country {
		display: none;
		@include rmin($md) {
			display: block; } }
	&__country-href {
		color: rgba(255, 255, 255, 0.87);
		position: relative;
		padding-left: 1rem;
		padding-right: 1rem;
		margin-left: 4.25rem;
		&:after {
			position: absolute;
			left: 0;
			content: ' ';
			width: 0.75rem;
			height: 1rem;
			background-image: url(../img/icon/city-select.svg);
			background-repeat: no-repeat;
			background-size: contain; }
		&:before {
			position: absolute;
			right: 0;
			bottom: 0;
			content: ' ';
			width: 0.75rem;
			height: 0.75rem;
			background-image: url(../img/icon/city-select-arr.svg);
			background-repeat: no-repeat;
			background-size: contain; } } }


// navigation
.navigation {
	background: #2E2423;
	padding: 0.5rem 0;
	display: none;
	@include rmin($md) {
		display: block; }
	&--main {
		background: transparent; }
	&__wrap {
		display: flex;
		flex-wrap: wrap;
		align-items: center;
		justify-content: space-between; }
	&__logo {
		max-width: 11rem;
		display: block;
		& img {
			width: 100%; } }
	&__menu {
		display: flex;
		align-items: center;
		margin-top: 0.5rem; }
	&__list {
		display: flex;
		align-items: center; }
	&__item {
		padding: 0.25rem 0;
		margin-left: 0.5rem;
		position: relativ;
		@include rmin($lg) {
			margin-left: 1.5rem; } }
	&__link {
		color: #ffffff;
		position: relative;
		&:after {
			content: ' ';
			width: 0;
			position: absolute;
			left: 0;
			bottom: -3px;
			height: 1px;
			background: #ffffff;
			@include tr(all ease 0.3s); }
		&:hover:after {
			width: 100%; } }
	&__slink {
		padding-right: 1rem;
		&:before {
			position: absolute;
			right: 0;
			bottom: 0.125rem;
			content: ' ';
			width: 0.75rem;
			height: 0.75rem;
			background-image: url(../img/icon/city-select-arr.svg);
			background-repeat: no-repeat;
			background-size: contain; } }
	&__submenu {
		position: absolute;
		display: none; } }

// main slider
.slider {
	position: relative;
	min-height: 100vh;
	padding-bottom: 5rem;
	background: #2E2423;
	@include rmin($xs) {
		padding-bottom: 2rem; }
	&__title {
		font-family: Ubuntu;
		font-weight: 500;
		color: #ffffff;
		padding-top: 2rem;
		font-size: 1.675rem;
		line-height: 1.4;
		text-align: center;
		@include rmin($xs) {
			padding-top: 4rem;
			font-size: 3rem;
			line-height: 4rem;
			max-width: 45rem;
			text-align: left; } }
	&__postitle {
		display: flex;
		justify-content: space-between;
		margin-top: 1.5rem; }
	&__advantages {
		display: flex;
		flex-wrap: wrap;
		max-width: 45rem;
		justify-content: space-between; }
	&__adv {
		position: relative;
		width: 100%;
		display: flex;
		align-items: center;
		@include rmin($xs) {
			width: calc(33% - 2rem);
			display: block;
			&:after {
				position: absolute;
				width: 2px;
				height: 55%;
				right: -1rem;
				top: 23%;
				content: ' ';
				background: $firstColor; } }
		&:last-child:after {
			display: none; }
		&-number {
			font-family: 'Neumann';
			font-size: 4rem;
			color: $firstColor;
			display: flex;
			align-content: center;
			align-items: center;
			flex: 0 0 10rem;
			@include rmin($xs) {
				color: #ffffff; }
			& span {
				font-family: Roboto;
				font-size: 1rem;
				padding-left: 1rem; } }
		&-descr {
			color: #ffffff;
			line-height: 1.6; } }

	&__scroll {
		max-width: 10rem;
		color: #ffffff;
		text-align: center;
		position: relative;
		display: none;
		@include rmin($md) {
			display: block; }
		&:after {
			position: absolute;
			bottom: -1.5rem;
			left: calc(50% - 0.6125rem);
			content: ' ';
			width: 1.25rem;
			height: 1.25rem;
			background-image: url(../img/icon/home-arr.svg);
			background-repeat: no-repeat;
			background-size: contain; }
		&-img {
			width: 4rem;
			margin: 0 auto; }
		&-title {
			font-weight: 700;
			font-family: 'Roboto Condensed';
			font-size: 1.5rem;
			padding: 0.25rem 0; } }
	&__gift {
		margin-top: 2rem;
		margin-bottom: 2rem;
		text-align: center;
		@include rmin($xs) {
			text-align: left; }
		&-title {
			color: #ffffff; }
		&-bold {
			color: #ffffff;
			font-weight: 700;
			font-size: 1.75rem; } }
	&__nav {
		display: flex;
		justify-content: center;
		align-content: center;
		align-items: center;
		position: absolute;
		bottom: 2rem;
		width: 100%; }
	&__arr {
		width: 1.25rem;
		height: 1.25rem;
		background-image: url(../img/icon/home-arr.svg);
		background-repeat: no-repeat;
		background-size: contain;
		cursor: pointer;
		&--left {
			transform: rotate(90deg); }
		&--right {
			transform: rotate(-90deg); } }
	&__dots {
		display: flex;
		align-content: center;
		align-items: center; }
	&__dot {
		width: 5px;
		height: 5px;
		border-radius: 50%;
		background: $firstColor;
		margin: 0 0.5rem;
		cursor: pointer;
		border: 1px solid $firstColor;
		@include tr(all ease 0.3s);
		&--active {
			width: 12px;
			height: 12px;
			border: 1px solid $firstColor;
			background: transparent; } } }


// main-services
.main-services {
	padding-top: 3rem;
	padding-bottom: 2rem;
	&__wrap {
		display: flex;
		flex-wrap: wrap;
		justify-content: space-between; }
	&__col {
		width: 100%;
		@include rmin($sm) {
			width: calc(50% - 0.5rem); }
		margin-bottom: 1rem; }
	&__block {
		display: flex;
		align-items: flex-end;
		justify-content: center;
		text-align: center;
		color: #ffffff;
		font-family: 'Roboto Condensed';
		font-weight: 700;
		font-size: 1.25rem;
		background-size: cover;
		padding: 0.75rem 2rem 1rem;
		line-height: 1.2;
		height: 22rem;
		position: relative;
		@include tr(transform ease 0.2s);
		&:after {
			opacity: 0.75;
			position: absolute;
			content: ' ';
			top: 0;
			left: 0;
			width: 100%;
			height: 100%;
			background: -moz-linear-gradient(top, rgba(135,224,253,0) 0%, rgba(65,108,121,0.13) 52%, rgba(0,0,0,1) 100%);
			background: -webkit-linear-gradient(top, rgba(135,224,253,0) 0%,rgba(65,108,121,0.13) 52%,rgba(0,0,0,1) 100%);
			background: linear-gradient(to bottom, rgba(135,224,253,0) 0%,rgba(65,108,121,0.13) 52%,rgba(0,0,0,1) 100%);
			filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#0087e0fd', endColorstr='#000000',GradientType=0 ); }
		&:hover {
			transform: scale(1.025); }
		& span {
			position: relative;
			z-index: 2; }
		&--house {
			background-image: url(../img/main-services1.jpg);
			height: 18rem;
			@include rmin($xs) {
				height: 24rem; }
			@include rmin($sm) {
				height: 30rem; } }
		&--material {
			background-image: url(../img/main-services2.jpg);
			height: 18rem;
			@include rmin($xs) {
				height: 24rem; }
			@include rmin($sm) {
				height: 14.5rem; } }
		&--works {
			background-image: url(../img/main-services3.jpg);
			height: 18rem;
			@include rmin($xs) {
				height: 24rem; }
			@include rmin($sm) {
				height: 14.5rem; } }
		&--bathhouse {
			background-image: url(../img/main-services4.jpg);
			height: 18rem;
			@include rmin($xs) {
				height: 24rem; }
			@include rmin($sm) {
				height: 14.5rem; } } } }

// main-production
.main-production {
	margin-top: 3rem;
	padding-top: 3rem;
	padding-bottom: 4rem;
	background: url(../img/main-production/main-production-bg.jpg);
	background-size: cover;
	&__wrap {
		display: flex;
		width: 100%;
		flex-wrap: wrap;
		@include rmin($md) {
			flex-flow: row nowrap; } }
	&__item {
		overflow: hidden;
		background-repeat: no-repeat;
		background-position: center;
		background-size: cover;
		position: relative;
		flex: 1 1 100%;
		height: 8rem;
		margin-bottom: 1rem;
		cursor: pointer;
		@include tr(all ease 0.3s);
		@include rmin($md) {
			flex: 1 1 16.66666667%;
			height: 30rem;
			margin-bottom: 0; }
		&:nth-child(1) {
			background-image: url(../img/main-production/1.jpg); }
		&:nth-child(2) {
			background-image: url(../img/main-production/2.jpg); }
		&:nth-child(3) {
			background-image: url(../img/main-production/3.jpg); }
		&:nth-child(4) {
			background-image: url(../img/main-production/4.jpg); }
		&:nth-child(5) {
			background-image: url(../img/main-production/5.jpg); }
		&:nth-child(6) {
			background-image: url(../img/main-production/6.jpg); } }

	&__block {
		width: 100%;
		height: 100%;
		position: absolute;
		top: 0;
		left: 0;
		height: 100%;
		width: 100%;
		display: flex;
		justify-content: center;
		align-items: center;
		flex-direction: column;
		& img {
			margin-bottom: 1rem;
			max-width: 3.5rem;
			@include rmin($md) {
				max-width: 4rem; } } }
	&__title {
		font-size: 1.25rem;
		font-weight: 700;
		font-family: 'Roboto Condensed';
		color: #ffffff; }
	&__text {
		background: rgba(0,0,0,.7);
		padding: 1.5rem;
		height: 100%;
		color: #ffffff;
		display: flex;
		align-content: center;
		align-items: center;
		opacity: 0;
		visibility: hidden;
		position: relative;
		z-index: 2;
		width: 100%;
		cursor: pointer;
		@include tr(all ease 0.3s);
		@include rmin($md) {
			width: 20rem; } }
	&__item--is-active &__text {
		opacity: 1;
		visibility: visible; }
	&__item {
		&--is-active {
			flex: 0.000000001 0.00000001 100%;
			height: auto;
			@include rmin($md) {
				flex: 0.000000001 0.00000001 20rem; } } } }



// main-creating
.main-creating {
	padding-top: 3rem;
	&__wrap {
		display: flex;
		flex-wrap: wrap;
		justify-content: space-between;
		background: url(../img/main-creating-bg.jpg);
		background-repeat: no-repeat;
		background-size: 300px;
		background-position: center top;
		min-height: 26rem;
		@include rmin($sm) {
			background-size: 420px; }
		@include rmin($md) {
			background-size: 30rem;
			background-position: right bottom; } }
	&__left {
		width: 100%;
		padding-top: 14rem;
		@include rmin($md) {
			width: 50%;
			padding-top: 0; } }
	&__right {
		width: calc(50% - 4rem);
		display: none; } }


// main-project
.main-project {
	padding-top: 3rem;
	padding-bottom: 4rem;
	@include rmin($lg) {
		padding-top: 0;
		margin-top: -3rem; }
	&__item {
		position: relative; }
	&__info {
		opacity: 1;
		@include tr(opacity ease 0.3s);
		text-align: center;
		position: absolute;
		top: 0;
		width: 100%;
		height: 100%;
		padding: 1rem;
		display: flex;
		flex-wrap: wrap;
		align-content: flex-end;
		align-items: flex-end;
		justify-content: center;
		background: -moz-linear-gradient(top, rgba(0,0,0,0) 0%, rgba(0,0,0,0) 51%, rgba(0,0,0,1) 100%);
		background: -webkit-linear-gradient(top, rgba(0,0,0,0) 0%,rgba(0,0,0,0) 51%,rgba(0,0,0,1) 100%);
		background: linear-gradient(to bottom, rgba(0,0,0,0) 0%,rgba(0,0,0,0) 51%,rgba(0,0,0,1) 100%);
		filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#00000000', endColorstr='#000000',GradientType=0 ); }
	&__title {
		width: 100%;
		font-family: 'Roboto Condensed';
		font-weight: 700;
		font-size: 1.125rem;
		color: #ffffff;
		margin-bottom: 0.125rem; }
	&__price {
		font-family: 'Roboto Condensed';
		font-weight: 700;
		font-size: 1.125rem;
		color: #ffffff;
		padding-left: 2rem;
		position: relative;
		display: inline-block;
		&:after {
			position: absolute;
			content: ' ';
			left: 0;
			width: 1.5rem;
			height: 1.5rem;
			background-image: url(../img/icon/price.svg);
			background-size: contain;
			background-repeat: no-repeat; } }
	&__overlay {
		position: absolute;
		top: 0;
		left: 0;
		background: rgba(0,0,0,0.7);
		width: 100%;
		height: 100%;
		padding: 1rem;
		display: flex;
		flex-wrap: wrap;
		flex-direction: column;
		justify-content: center;
		align-items: center;
		align-content: center;
		text-align: center;
		opacity: 0;
		@include tr(opacity ease 0.3s); }
	&__btn {
		color: #ffffff;
		width: auto;
		background: transparent;
		margin-top: 1.5rem;
		padding: 0.5rem 2rem; }
	&__item:hover &__info {
		opacity: 0; }
	&__item:hover &__overlay {
		opacity: 1; }
	&__nav {
		margin-top: 1rem;
		justify-content: center;
		align-items: center;
		display: none;
		@include rmin($sm) {
			display: flex; } }
	&__gbtn {
		display: flex;
		justify-content: center;
		@include rmin($sm) {
			display: none; } }
	&__arr {
		width: 1.5rem;
		height: 1.5rem;
		background-image: url(../img/icon/home-arr.svg);
		background-repeat: no-repeat;
		background-size: contain;
		cursor: pointer;
		&--left {
			transform: rotate(90deg); }
		&--right {
			transform: rotate(-90deg); } }
	&__counter {
		font-family: 'Neumann';
		display: flex;
		align-items: flex-end;
		margin-left: 0.5rem;
		margin-right: 0.5rem;
		&-separator {
			font-size: 2rem;
			padding: 0 0.25rem; }
		&-current {
			font-size: 2rem; }
		&-total {
			line-height: 2rem; } } }


// calculator
.calculator {
	padding-top: 3rem;
	padding-bottom: 3rem;
	background-image: url(../img/calculator/calculator.jpg);
	background-repeat: no-repeat;
	background-size: cover;
	&__navs {
		display: flex;
		justify-content: center; }
	&__nav {
		width: 2rem;
		height: 2rem;
		color: $firstColor;
		border: 1px solid $firstColor;
		border-radius: 50%;
		cursor: pointer;
		display: flex;
		justify-content: center;
		align-items: center;
		@include tr(background ease 0.2s);
		margin: 0 0.375rem;
		position: relative;
		margin-bottom: 2rem;
		&:after {
			position: absolute;
			content: ' ';
			top: 50%;
			right: -0.75rem;
			width: 0.75rem;
			height: 1px;
			background: $firstColor; }
		&:last-child:after {
			display: none; }
		//&:hover
		//	background: rgba(223, 188, 130, 0.3)
		&--active {
			background: rgba(223, 188, 130, 0.3); } }
	&__subtitle {
		color: #ffffff;
		font-size: 1.5rem;
		font-family: 'Roboto Condensed';
		font-weight: 700;
		text-align: center; }
	&__list {
		display: flex;
		flex-wrap: wrap;
		justify-content: center;
		margin-top: 2rem; }
	&__item {
		padding: 0 1rem;
		text-align: center;
		width: 50%;
		max-width: 15rem;
		margin-bottom: 2rem;
		@include rmin($xs) {
			width: 100%; } }
	&__img {
		max-width: 6rem;
		height: 6rem;
		margin: 0 auto;
		display: block;
		@include rmin($xs) {
			max-width: 8rem;
			height: 8rem; } }
	&__radio {
		color: #ffffff;
		margin-top: 1rem;
		& label {
			margin-left: 0.125rem; }
		& input[type="radio"] {
    		display: none; }
		& input[type="radio"] + label {
			position: relative;
			padding-left: 1.5rem; }
		& input[type="radio"] + label:after {
			content: ' ';
			position: absolute;
			top: 0;
			left: 0;
			background: transparent;
			height: 1rem;
			width: 1rem;
			border: 3px solid transparent;
			border-radius: 50%;
			display: inline-block;
			padding: 0 0 0 0px;
			box-shadow: 0px 0px 0px 1px $firstColor; }

		& input[type="radio"]:checked + label:after {
			background: url(../img/calculator/check.svg);
			background-repeat: no-repeat;
			background-position: center center;
			background-size: 0.5rem;
			height: 16px;
			width: 16px;
			display:inline-block {}
			padding: 0 0 0 0px; } }
	&__btn {
		margin-top: 1rem;
		text-align: center; } }


