		&-btn {
			background: transparent;
			color: #ffffff; }
		&-title {
			font-family: 'Roboto Condensed';
			font-size: 1.25rem;
			font-weight: 700;
			margin-bottom: 0.25rem;
			text-align: center; }
		&-subtitle {
			margin-bottom: 1.25rem; } }
	&__item:hover &__overlay {
		opacity: 1; } }

// main-why
.main-why {
	padding-top: 3rem;
	padding-bottom: 2rem;
	//margin-top: 3rem
	background-image: url(../img/why-bg.jpg);
	background-size: cover;
	text-align: center;
	&__wrap {
		display: flex;
		flex-wrap: wrap;
		justify-content: space-between; }
	&__item {
		margin-bottom: 3rem;
		width: 100%;
		@include rmin($sm) {
			width: calc(50% - 1rem); }
		@include rmin($md) {
			width: calc(33.33% - 1rem); } }
	&__icon {
		text-align: center;
		margin-bottom: 1.5rem;
		& img {
			margin: 0 auto;
			max-height: 4.5rem;
			@include rmin($xs) {
				max-height: 5.5rem; } } }
	&__title {
		color: #ffffff;
		font-family: 'Roboto Condensed';
		font-size: 1.375rem;
		@include rmin($md) {
			font-size: 1.5rem; }
		font-weight: 700; }
	&__subtitle {
		color: #ffffff;
		line-height: 1.5;
		margin-top: 0.5rem; } }

// main-order
.main-order {
	padding-top: 3rem;
	padding-bottom: 1rem;
	&__subtitle {
		max-width: 64rem;
		text-align: center;
		margin: 0 auto; } }

// main-contact
.main-contact {
	padding-top: 4rem;
	padding-bottom: 4rem;
	margin-top: 3rem;
	background-image: url(../img/main-contact.jpg);
	background-position: center center;
	&__wrap {
		display: flex;
		flex-wrap: wrap;
		justify-content: space-between; }
	&__item {
		color: #ffffff;
		margin-bottom: 1.5rem;
		margin-right: 2rem;
		width: 100%;
		@include rmin($sm) {
			width: calc(50% - 2rem); }
		@include rmin($md) {
			width: calc(33.33% - 2rem); }
		@include rmin($lg) {
			width: auto;
			&:last-child {
				margin-right: 0; } }
		& p {
			margin-bottom: 0.25rem; } }
	&__social {
		display: flex;
		&-link {
			display: block;
			width: 1.75rem;
			height: 1.75rem;
			margin-right: 1rem; } } }







// copyright
.copyright {
	padding: 1rem 0;
	background: #2E2423;
	&__wrap {
		display: flex;
		justify-content: space-between; }
	&__left {
		color: #ffffff;
		text-align: left; }
	&__right {
		color: #ffffff;
		text-align: right;
		& a {
			color: #ffffff;
			@include tr(all ease 0.2s);
			&:hover {
				color: $firstColor; } } } }
