$xs: 576;
$sm: 768;
$md: 992;
$lg: 1200;

// colors
$firstColor: #DFBC82;

html {
    font-size: 14px;
    @include rmin($sm) {
        font-size: 15px; }
    @include rmin($md) {
        font-size: 16px; } }
body {
	font-family: 'Roboto', sans-serif;
	font-weight: 300;
	font-size: 1rem;
	line-height: 1.4 {
    background: #ccc; } }

// General
img {
	max-width: 100%;
	font-size: 0;
	line-height: 0;
	display: block; }

a {
	text-decoration: none;
	&:hover {
		text-decoration: none; } }

p {
	margin-bottom: 1rem; }

.container {
    width: 100%;
    max-width: 540px;
    margin: 0 auto;
    padding: 0 20px;
    @include rmin($sm) {
        max-width: 720px; }
    @include rmin($md) {
        max-width: 960px; }
    @include rmin($lg) {
        max-width: 1170px; } }


.title {
    font-family: 'Ubuntu';
    font-size: 1.75rem;
    color: #000000;
    text-align: center;
    margin-bottom: 3rem;
    @include rmin($sm) {
        font-size: 2.5rem; }
    &--white {
        color: #ffffff; } }

// BTN
.btn {
    display: inline-block;
    padding: 0.75rem 1rem;
    max-width: 15rem;
    width: 100%;
    color: #000000;
    text-align: center;
    font-family: 'Ubuntu';
    font-size: 1rem;
    background: $firstColor;
    border: 1px solid $firstColor;
    display: block;
    @include tr(all ease 0.2s);
    &:hover {
        background: #ffffff; }
    &--center {
        margin: 0 auto; }
    &--mcenter {
        margin: 0 auto;
        @include rmin($xs) {
            margin: 0; } }
    &--mb {
        margin-bottom: 1rem !important; }
    &--revers {
        background: #ffffff;
        &:hover {
            background: $firstColor; } }
    &__group {
        display: flex;
        flex-wrap: wrap;
        //justify-content: space-between
        margin-top: 2rem;
        & .btn {
            width: calc(50% - 1rem);
            margin-right: 1rem;
            &:last-child {
                margin-right: 0; } } } }

// form
.form {
    &__group {
        text-align: center;
        &--col {
            display: flex;
            flex-wrap: wrap;
            justify-content: space-between;
            max-width: 56rem;
            margin: 2rem auto 0; } }
    &__input {
        border-top: 0;
        border-left: 0;
        border-right: 0;
        border-bottom: 2px solid #cccccc;
        width: 100%;
        font-size: 1rem;
        color: #333333;
        padding: 0.25rem 0;
        margin-bottom: 1.5rem;
        text-align: center;
        @include tr(all ease 0.2s);
        @include rmin($xs) {
            margin-bottom: 0;
            text-align: left; }
        &:focus {
            border-bottom: 2px solid $firstColor; }
        &--col {
            width: 100%;
            @include rmin($xs) {
                width: calc(33.33% - 1rem); } } } }


